<template>
  <div class="main-body">
    <div class="left-menu">
      <div class="left-menu-title">
        数据集
      </div>
      <div class="left-menu-content">
        <div class="menu-item" :class="{ selected: menuSelectedIndex === index }" v-for="(n, index) in menuList"
          :key="index" @click="getMenuInfo(index)">
          <div class="menu-name" v-if="editMenuIndex !== index">{{ n.name }}</div>
          <div class="menu-name" v-else>
            <el-input @click.stop v-model="menuList[index].nameText" @keydown="(e) => { saveMenuChange(e, index) }" />
          </div>
          <div class="menu-handle">
            <div class="menu-handle-btn" @click.stop="editMenuIndex = index" v-if="editMenuIndex !== index">
              编辑
            </div>
            <div class="menu-handle-btn" @click.stop="saveMenuChange({ keyCode: 13 }, index)" v-else>
              保存
            </div>
            <div class="menu-handle-btn danger" @click.stop="deleteMenu(index)">
              删除
            </div>
          </div>
        </div>
        <div class="menu-item add" @click="addMenu">
          添加
        </div>
      </div>
    </div>
    <div class="left-sub-menu" v-if="menuSelectedIndex !== null">
      <div class="left-sub-menu-title">
        <el-tag type="success">
          已处理<span class="success">{{ menuList[menuSelectedIndex].labeled }}</span>张
        </el-tag>
        <el-tag type="danger">
          未处理<span class="danger">{{ menuList[menuSelectedIndex].unlabeled }}</span>张
        </el-tag>
      </div>
      <div class="left-sub-menu-content">
        <el-collapse v-model="subMenuType" accordion>
          <el-collapse-item title="未处理图片" name="1">
            <div class="upload-area">
              <el-upload class="avatar-uploader" :before-upload="beforeUpload" accept="image/*"
                :data="{ dataset: menuList[menuSelectedIndex].name }" :action="`${httpRequestUrl}/labels/upload`"
                :on-success="() => { getMenuList(); getSubMenuInfo(menuSelectedIndex) }" :show-file-list="false">
                <!-- <img v-if="imageUrl" :src="imageUrl" class="avatar" /> -->
                <el-icon class="avatar-uploader-icon">
                  <Plus />
                </el-icon>
              </el-upload>
            </div>
            <div class="page-area" v-if="menuList[menuSelectedIndex].unlabeled > type1PageSize">
              <el-pagination @current-change="currentChange" v-model="type1Page" :page-size="type1PageSize" small
                layout="prev, pager, next" :total="menuList[menuSelectedIndex].unlabeled" class="mt-4" />
            </div>
            <div class="left-sub-menu-item" v-for="(imageItem, imageIndex) in imageList" :key="imageIndex">
              <div class="left-sub-menu-image" v-if="subMenuType === '1'" @click="getImageInfo(imageItem)">
                <el-image style="width: 100px; height: 100px"
                  :src="`${httpRequestUrl}/labels/static/${menuList[menuSelectedIndex].name}/${imageItem}`"
                  fit="contain" />
                <div class="delete-btn">
                  <el-icon class="delete-info" @click.stop="delImage(imageItem)">
                    <Delete />
                  </el-icon>
                </div>
              </div>
            </div>
          </el-collapse-item>
          <el-collapse-item title="已处理图片" name="2">
            <div class="page-area" v-if="menuList[menuSelectedIndex].labeled > type2PageSize">
              <el-pagination @current-change="currentChange" :page-size="type2PageSize" small layout="prev, pager, next"
                :total="menuList[menuSelectedIndex].labeled" class="mt-4" />
            </div>
            <div class="left-sub-menu-item" v-for="(imageItem, imageIndex) in imageList" :key="imageIndex">
              <div class="left-sub-menu-image" v-if="subMenuType === '2'" @click="getImageInfo(imageItem)">
                <el-image style="width: 100px; height: 100px"
                  :src="`${httpRequestUrl}/labels/static/${menuList[menuSelectedIndex].name}/${imageItem}`"
                  fit="contain" />
                <div class="delete-btn">
                  <el-icon class="delete-info" @click.stop="delImage()">
                    <Delete />
                  </el-icon>
                </div>
              </div>
            </div>
          </el-collapse-item>
        </el-collapse>
      </div>
    </div>
    <div class="content" v-if="imageSelected">
      <div class="content-image-area">
        <el-image style="width: 400px; height: 400px;position:absolute;top:0;left:0;bottom:0;right:0;margin:auto;"
          :preview-src-list="[`${httpRequestUrl}/labels/static/${menuList[menuSelectedIndex].name}/${imageSelected}`]"
          :src="`${httpRequestUrl}/labels/static/${menuList[menuSelectedIndex].name}/${imageSelected}`" fit="contain" />
      </div>
      <div class="content-image-msg">
        <div class="content-msg-title">
          图片信息
        </div>
        <div class="content-dataset">
          <label>所属数据集</label>
          <div class="content-other">
            <el-select v-model="imageDataset" @change="changeImageDataset">
              <el-option v-for="n in menuList" :key="n.name" :value="n.name" :label="n.name"></el-option>
            </el-select>
          </div>
        </div>
        <div class="content-prompt">
          <label>提示词</label>
          <div class="content-other">
            <el-input v-model="imagePrompt" style="width: 400px;" :rows="5" type="textarea"
              placeholder="请使用,分割每个prompt" />
          </div>
          <div class="prompt-handle-area">
            <div class="prompt-translate-handle" @click="getTranslateMsg">
              翻译
            </div>
            <div class="prompt-save-handle" @click="saveLabel">
              保存
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="prompts-list" v-if="imageSelected">
      <el-tabs style="width:" v-model="promptsListSelected" class="demo-tabs" @tab-click="handleClick">
        <el-tab-pane :label="n.name" :name="n.name" v-for="n in promptsList" :key="n.name">
          <el-tag style="margin-right: 10px;margin-bottom:10px;cursor:pointer;" v-for="(prompt, promptIndex) in n.list"
            :key="promptIndex" @click="imagePrompt += `${imagePrompt ? ',' : ''}${prompt}`">
            {{ prompt }}
          </el-tag>
        </el-tab-pane>
      </el-tabs>
    </div>
    <el-dialog destroy-on-close v-model="translateVisible" title="翻译结果" width="440px">
      <el-input v-model="translateMsg" style="width: 400px;" :rows="5" type="textarea" placeholder="翻译结果" />
      <template #footer>
        <div class="dialog-footer">
          <el-button @click="closeTranslateDialog">取消</el-button>
          <el-button type="primary" @click="replacePrompt">
            替换
          </el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>
<script>
import { Plus, Delete } from '@element-plus/icons-vue'
import axios from 'axios'
const httpRequestUrl = 'https://u248120-a5f1-770998cc.westb.seetacloud.com:8443'
export default {
  data() {
    return {
      httpRequestUrl,
      editMenuIndex: null,
      menuSelectedIndex: null,
      subMenuType: "1",
      type1Page: 1,
      type1PageSize: 10,
      type2Page: 1,
      type2PageSize: 10,
      menuList: [],
      imageList: [],
      imageSelected: null,
      imagePrompt: null,
      imageDataset: null,
      promptsList: [],
      promptsListSelected: '',
      translateMsg: '',
      translateVisible: false
    }
  },
  components: {
    Plus,
    Delete
  },
  mounted() {
    this.getPromptsList()
    this.init()
  },
  watch: {
    subMenuType() {
      this.getSubMenuInfo(this.menuSelectedIndex)
    }
  },
  methods: {
    init() {
      // this.imageUrl = null
      this.editMenuIndex = null
      this.menuSelectedIndex = null
      this.imageSelected = null
      this.getMenuList()
    },
    getMenuList() {

      axios.get(`${httpRequestUrl}/labels/get_datasets`).then(res => {
        console.log(res)
        const { data } = res
        this.menuList = Object.keys(data).map(n => {
          return Object.assign({
            name: n,
            nameText: n
          }, data[n])
        })
        // this.dataList = data.images.map(n => {
        //   return `${httpRequestUrl}/static/${n}`
        // })
        // console.log(data)
      })
    },
    saveMenuChange(event, index) {
      if (event.keyCode === 13) {
        if (this.menuList[index].nameText === this.menuList[index].name) {
          return
        }
        if (this.menuList.filter(n => n.name === this.menuList[index].nameText).length) {
          this.$message.error('数据集名称不可重复')
          return
        }
        console.log('保存成功')
        axios.post(`${httpRequestUrl}/labels/rename_dataset`, {
          dataset: this.menuList[index].name,
          name: this.menuList[index].nameText
        }).then(() => {
          this.$message.success('修改成功')
          this.editMenuIndex = null
          this.init()
        })
        
      }
      console.log(event, index)
    },
    addMenu() {
      this.$prompt('请输入数据集名称', '提示', {
        confirmButtonText: '保存',
        cancelButtonText: '取消',
        inputErrorMessage: '请输入数据集名称',
      }).then(({ value }) => {
        if (!value) {
          this.$message.error('请输入数据集名称')
          return
        }
        if (this.menuList.filter(n => n.name === value).length) {
          this.$message.error('数据集名称不可重复')
          return
        }
        axios.post(`${httpRequestUrl}/labels/create_dataset`, {
          dataset: value
        }).then(() => {
          this.$message.success('新建成功')
          this.init()
        })
        console.log(value)
      }).catch(() => {
        console.log('取消操作')
      })
    },
    deleteMenu(index) {
      console.log(index)
      this.$confirm(
        '此操作不可逆，是否继续',
        '警告',
        {
          confirmButtonText: '确认',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).then(() => {
        axios.post(`${httpRequestUrl}/labels/delete_dataset`, {
          dataset: this.menuList[index].name
        }).then(() => {
          this.$message.success('成功删除数据集')
          this.init()
        })
      }).catch(() => {
        this.$message('已取消删除数据集')
      })
    },
    getMenuInfo(index, type = 0) {
      if (this.menuSelectedIndex === index && !type) {
        return
      }
      this.menuSelectedIndex = index
      this.subMenuType = '1'
      this.type1Page = 1
      this.type2Page = 1
      this.imageSelected = null
      this.getSubMenuInfo(index)
    },
    beforeUpload(file) {
      if (file.size > 1024 * 1024 * 10) {
        this.$message.error('请上传10Mb以内的图片')
        return false
      }
    },
    currentChange(val) {
      console.log(val)
      this.subMenuType === '1' ? this.type1Page = val : this.type2Page = val
      this.getSubMenuInfo(this.menuSelectedIndex)
    },
    getSubMenuInfo(index) {
      this.imageList = []
      let page = this.subMenuType === '1' ? this.type1Page : this.type2Page
      axios.post(`${httpRequestUrl}/labels/query`, {
        dataset: this.menuList[index].name,
        page,
        page_size: this.subMenuType === '1' ? this.type1PageSize : this.type2PageSize,
        label_type: this.subMenuType === '1' ? 'unlabeled' : 'labeled',
      }).then(res => {
        console.log(res)
        this.imageList = res.data
        if (!this.imageList.length && page > 1) {
          this.subMenuType === '1' ? this.type1Page = this.type1Page - 1 : this.type2Page = this.type2Page - 1
          this.getSubMenuInfo(this.menuSelectedIndex)
        }
      })
    },
    delImage(imageItem) {
      this.$confirm(
        '此操作不可逆，是否继续',
        '警告',
        {
          confirmButtonText: '确认',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).then(() => {
        axios.post(`${httpRequestUrl}/labels/delete_image`, {
          dataset: this.menuList[this.menuSelectedIndex].name,
          label_type: this.subMenuType === '1' ? 'unlabeled' : 'labeled',
          image_path: imageItem
        }).then(() => {
          this.$message.success('删除图片成功')
          this.getMenuList()
          this.getSubMenuInfo(this.menuSelectedIndex)
          this.imageSelected = null
        })
        console.log('删除')
      }).catch(() => {
        this.$message('已取消删除图片')
      })
    },
    getImageInfo(imageItem) {
      console.log(imageItem)
      this.imageSelected = imageItem
      axios.post(`${httpRequestUrl}/labels/image_details`, {
        dataset: this.menuList[this.menuSelectedIndex].name,
        image_path: imageItem
      }).then((res) => {
        console.log(res)
        this.imagePrompt = res.data.prompt || ''
        this.imageDataset = this.menuList[this.menuSelectedIndex].name
        if (this.promptsList.length) {
          this.promptsListSelected = this.promptsList[0].name
        } else {
          this.promptsListSelected = ''
        }
      })
    },
    changeImageDataset() {
      if (this.imageDataset === this.menuList[this.menuSelectedIndex].name) {
        return
      }
      this.$confirm(
        `您正在将此图片移动至《${this.imageDataset}》数据集，是否继续`,
        '警告',
        {
          confirmButtonText: '确认',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).then(() => {
        axios.post(`${httpRequestUrl}/labels/move`, {
          dataset: this.menuList[this.menuSelectedIndex].name,
          image_path: this.imageSelected,
          target_dataset: this.imageDataset
        }).then(() => {
          this.$message.success('移动成功')
          this.getMenuList()
          this.getMenuInfo(this.menuSelectedIndex, 1)
          this.imageSelected = null
        })
      }).catch(() => {
        this.imageDataset = this.menuList[this.menuSelectedIndex].name
        this.$message('已取消移动')
      })
    },
    getPromptsList() {
      axios.get(`${httpRequestUrl}/labels/prompt_helper`).then(res => {
        this.promptsList = Object.keys(res.data).map(n => {
          return {
            name: n,
            list: res.data[n]
          }
        })
        if (this.promptsList.length) {
          this.promptsListSelected = this.promptsList[0].name
        } else {
          this.promptsListSelected = ''
        }
      })
    },
    getTranslateMsg() {
      axios.post(`${httpRequestUrl}/labels/translate`, {
        prompt: this.imagePrompt
      }).then(res => {
        console.log(res)
        this.translateMsg = res.data
        this.translateVisible = true
        // this.$prompt('翻译结果', '提示', {
        //   confirmButtonText: '替换',
        //   cancelButtonText: '取消',
        //   inputErrorMessage: '请输入数据集名称',
        //   inputValue: res.data
        // }).then(({ value }) => {
        //   // console.log(value)
        // 
        // }).catch(() => {
        //   console.log('取消操作')
        // })
      })
    },
    replacePrompt() {
      this.imagePrompt = this.translateMsg
      this.translateVisible = false
      this.$message.success('替换成功')

    },
    closeTranslateDialog() {
      this.translateVisible = false
      this.$message('取消操作')
    },
    saveLabel() {
      axios.post(`${httpRequestUrl}/labels/label`, {
        dataset: this.menuList[this.menuSelectedIndex].name,
        image_path: this.imageSelected,
        prompt: this.imagePrompt
      }).then(() => {
        this.$message.success('保存成功')
        this.getMenuList()
        this.getMenuInfo(this.menuSelectedIndex, 1)
        this.imageSelected = null
      })
    }
  }
}
// TODO: dataset名称修改bug
</script>
<style>
.el-textarea__inner {
  resize: none;
}
</style>
